<h2 id="nested-modals">Nested modals <a class="header-anchor" href="#nested-modals" aria-hidden="true">&#128279;</a></h2>
<p>Modals can be <strong>nested</strong> since 0.15.</p>
<p>Click on the button below to open the entry of a set of nested modals.</p>
